html{
  font-size: 62.5%; /* 16px * 62.5 = 10px = 1rem */
  box-sizing: border-box;
}
body {
  font: 1.6rem Helvetica;
  height: 100vh;
  margin: 0;

}
button{
  font-size: 1.6rem;
}
.full-width{
  width: 100%;
}
#root {
  height: 100%;
}
.grid-container{
  display: grid;
  grid-template-areas: 
  "header"
  "main"
  "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}
.header{
  grid-area: header;
  background-color: #203040;
  color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: .5rem;
}
.brand a{
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: bold;
  text-decoration: none;
}
.header-links a{
  color: #ffffff;
  text-decoration: none;
  margin-right: 1rem;
  padding: 0;
}
.header-links a:hover{
  color: #ff8000;
}
.main{
  grid-area: main;
}
.footer{
  grid-area: footer;
  background-color: #203040;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*home screen */
.products {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
a {
  text-decoration: none;
}
a:hover {
  color: #ff8000;
}
.products li{
  flex: 0 1 34rem;
  margin: 1rem;
  height: 50rem;
  border-bottom: .1rem #c0c0c0 solid;
  list-style-type: none;
  padding: 0;
}
.product {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.product-image{
  max-width: 34rem;
  max-height: 34rem;
}
.product-name {
  font-size: 2rem;
  font-weight: bold;
}
.product-brand {
  font-size: 1.2rem;
  color: #808080;
}
.product-price {
  font-size: 2rem;
  font-weight: bold;
}
.product-rating {
  margin-bottom: 1rem;
}

/*Stylebar*/
.brand button{
   font-size: 3rem;
   padding: .5rem;
   background: none;
   border: none;
   color: white;
   cursor: pointer;
}
.sidebar {
   position: fixed;
   transition: all .5s;
   transform: translateX(-30rem);
   width: 30rem;
   background-color: #f0f0f0;
   height: 100%;
}
.sidebar.open {
   transform: translateX(0);
}
.sidebar-close-button{
  border-radius: 50%;
  border: .1rem black solid;
  width: 3rem;
  height: 3rem;
  padding: .5rem;
  font-size: 2rem;
  padding-top: 0;
  cursor: pointer;
  position: absolute;
  right: .5rem;
  top: 1.5rem;
}

/*Product Details*/
.details {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1rem;
}
.details-image {
  flex: 2 1 60rem;
}
.details-image img{
  max-width: 60rem;
  width: 100%;
}
.details-info {
  flex: 1 1 30rem;
}
.details-actions {
  flex: 1 1 30rem;
}

.details-info ul, .details-actions ul {
  list-style-type: none;
  padding: 0;
}
.details-info li, .details-actions li {
 margin-bottom: 1rem;
}
.back-to-result {
  padding: 1rem;
}
.details-actions {
  border: .1rem #808080 solid;
  border-radius: .5rem;
  background-color: #f8f8f8;
  padding: 1rem;
}
.details-actions ul li:last-child {
  display: flex;
  flex-direction: column;
}
.button {
  padding: 1rem;
  border: .1rem #808080 solid;
  border-radius: .5rem;
  cursor: pointer;
  }
.button.primary{
  background-color: #f0c040;
}
.button.secondary{
  background-color: #c0f0f0;
}
.text-center{

text-align: center;
}
.button:hover {
  border: .1rem #404040 solid;
}
/* Cart */
.cart {
  display: flex;
  flex-wrap: wrap;
  margin: 1rem;
  align-items: flex-start;
}
.cart-list {
  flex: 3 1 60rem;

}
.cart-action{
  flex:1 1 20rem;
  background-color: #f8f8f8;
  border-radius: .5rem;
  padding: 1rem;
}
.cart-list-container {
  padding: 0;
  list-style-type: none;
  padding: 1rem;
}
.cart-list-container li {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: .1rem #808080 solid;
}
.cart-list-container li:first-child {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
.cart-list-container li img{
  max-width: 10rem;
  max-height: 10rem;
  
}
.cart-image{
  flex: 1 1;
}
.cart-name {
  flex: 8 1;
}
.cart-price{
  flex: 1 1;
  font-size: 2.5rem;
  text-align: right;
}
/*forms*/

.form{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.form-container{
  display: flex;
  flex-direction: column;
  width: 32rem;
  padding: 2rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  list-style-type: none;
}
.form-container li{
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
  margin-top: 1rem;
}
input{
  padding: 1rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem; 
}

/*products*/
.product-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.content-margined{
  margin: 1rem;
}

.table  {
  width: 100%;
}
th {
  text-align: left;
}
tbody>tr:nth-child(odd){
  background-color: #f0f0f0;
}

/*Checkout-steps*/

.checkout-steps{
  display: flex;
  justify-content:space-evenly;
  width: 43rem;
  margin: 1rem auto;
}
.checkout-steps > div{
  border-top: .3rem #c0c0c0 solid;
  color: #c0c0c0;
  flex: 1 1;
  padding: 1rem;
}
.checkout-steps > div.active{
  border-top: .3rem #f08000 solid;
  color: #f08000;
}

/*placeorder*/
 .placeorder{
   display: flex;
   flex-wrap: wrap;
   padding: 1rem;
   justify-content: space-between;

 }
 .placeorder-info{
   flex: 3 1 60rem;
   
 }
 .placeorder-action{
   flex: 1 1 20rem;
   border: .1rem #c0c0c0 solid;
   border-radius: .5rem;
   background-color: #fcfcfc;
   padding: 1rem;

 }
 
 .placeorder-info > div{
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  background-color: #fcfcfc;
  padding: 1rem;
  margin: 1rem;
}
.placeorder-info > div:first-child{
  margin-top: 0;
}

.placeorder-action > ul {
  padding: 0;
  list-style-type: none;
}
 .placeorder-action > ul >li{
   display: flex;
   justify-content: space-between;
   margin-bottom: 1rem;
 }

 .placeorder-action > ul >li:last-child{
   font-size: 2rem; 
   color: #c04000;
   font-weight: bold;
 }